.category-navbar {
    padding: 10px 0;
    height: 40px;
    margin-bottom: 20px;
}

.category-list {
    display: flex;
    justify-content: flex-start;
    margin: 0;
    padding: 0;
    list-style: none;
}

.category-item {
    font-weight: bold;
    padding: 10px 15px;
    margin: 10px 50px 10px 10px;
    opacity: 0.7;
    cursor: pointer;
    transition: opacity 0.3s, border-bottom 0.3s;
}

.category-item:hover {
    border-bottom: 3px solid #000;
    opacity: 1;
}